import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { ColumnsProps, DefaultSortProps, enumColumn, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Ppp } from '../../../../models/dtos/mfp/ppp';
import { CATEGORIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'vinculo.matricula',
    order: OrderType.ASC,
  },
];

const PppSearchView: FC<SearchViewProps<Ppp>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'vinculo.matricula',
      editable: false,
      sort: true,
      text: 'Matr√≠cula',
    },
    {
      dataField: 'nome',
      editable: false,
      sort: true,
      text: 'Trabalhador',
    },
    enumColumn(
      {
        dataField: 'vinculo.categoria',
        text: 'Categoria',
      },
      CATEGORIA_ENUM_VALUES
    ),
    {
      dataField: 'brPdh',
      editable: false,
      sort: true,
      text: 'BR/PDH',
    },
  ];

  return (
    <WindowComponent
      title="Manuten√ß√£o de Perfil Profissiogr√°fico Previdenci√°rio"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'PppEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default PppSearchView;
